body {
  font-family: sans-serif;
}

#main {
  max-width: 600px;
  margin: 2rem auto;
}

#users {
  list-style: none;
  list-style-type: none;
  width: 100%;
  padding: 0;
  margin: 0;
  margin-top: 1rem;
  gap: 0.5rem;
}

#users li {
  display: flex;
  justify-content: space-between;
  list-style: none;
  list-style-type: none;
  margin: 0;
  border: 1px solid #ccc;
  padding: 1rem;
}
